<template>
  <div class="">
    <div class="title">消费项目</div>
    <el-radio-group v-model="action">
      <el-radio-button v-for="(item,key) in content_list.action" :key="key" :label="key"></el-radio-button>
    </el-radio-group>
    <el-select v-model="action" filterable allow-create placeholder="请选择消费项目" style="margin:5px">
      <el-option v-for="(item,key) in content_list.action_list" :key="key" :label="key" :value="key">
      </el-option>
    </el-select>

    <div class="title">消费内容</div>
    <el-checkbox-group v-model="things">
      <el-checkbox v-for="thing in things_list" :label="thing" :key="thing">{{thing}}</el-checkbox>
    </el-checkbox-group>
    <el-select v-model="things" multiple filterable allow-create placeholder="请选择消费内容" style="margin:5px">
      <el-option v-for="item in things_more_list" :key="item" :label="item" :value="item">
      </el-option>
    </el-select>

    <div class="title">金额</div>
    <el-input size="large" placeholder="输入金额" v-model="money" style="width:80%;margin:5px">
    </el-input>

    <div class="title">对象</div>
    <el-radio-group v-model="who" style="margin:5px">
      <el-radio-button v-for="item in content_list.who" :key="item" :label="item"></el-radio-button>
    </el-radio-group>

    <div class="title">地点</div>
    <el-select v-model="where" filterable allow-create placeholder="请选择地点" style="margin:5px">
      <el-option v-for="item in content_list.where" :key="item" :label="item" :value="item">
      </el-option>
    </el-select>

    <div class="submit-button" @click="submit_data()">提交</div>
  </div>
</template>

<script>
import { content_list } from "../assets/js/content_list";

export default {
  name: 'upload',
  data() {
    return {
      action: '',
      things: [],
      money: 0,
      who: 'ALL',
      where: '北京',
      things_list: [],
      things_more_list: [],
      content_list: {}
    }
  },
  watch: {
    action: function() {
      this.click_action(this.action);
    }
  },
  methods: {
    click_action: function(item) {
      if (item == "")
        return;
      this.things = [];
      if (this.content_list.action[item]) {
        this.things_list = this.content_list.action[item][0];
        this.things_more_list = this.content_list.action[item][1];
      } else if(this.content_list.action_list[item]){
        this.things_list = this.content_list.action_list[item][0];
        this.things_more_list = this.content_list.action_list[item][1];
      }
    },
    submit_data: function() {
      var data = {
        action:this.action,
        things:this.things,
        money:this.money,
        who:this.who,
        where:this.where
      }
      console.log(data);
    }
  },
  mounted: function() {
    this.content_list = content_list;
    this.action = "早饭";
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.title {
  margin: 5px;
  border: 1px solid steelblue;
  border-radius: 5px;
  color: steelblue;
}

.submit-button {
  margin: 20px 10px 0 10px;
  height: 50px;
  line-height: 50px;
  background: rgba(0, 200, 0, 0.7);
  border-radius: 5px;
  color: white;
}

.submit-button:active {
  background: rgba(0, 255, 0, 0.3);
  color: gray;
}
</style>



